



















































	import { Component, Vue } from 'vue-property-decorator';
	import { AlertsModule } from '@/store/modules/alerts';
	import { AppModule } from '@/store/modules/app';
	import { WebsocketModule } from '@/store/modules/websocket';
	// import {MD5} from '@/lib/md5'

	@Component
	export default class Start extends Vue {
	  isMobile = AppModule.isMobile;
	  fab = true;

		get fabUsersStyle() {
			const marginRight = AppModule.isMobile ? 30 : 50;
			return {
				position: 'fixed',
				marginRight: `${marginRight}px`,
				marginTop: `44px`,
			};
		}

	  onSpeedDial() {
		  this.fab = !this.fab
	  }

    onTest(){
			WebsocketModule.serverMethod({ 'method': 'ping' })
				.then(data => console.log(data))
				.catch(e => console.error(e));
    }

	}
